.heptagon-div{
	width: 50%;
	height: 98vh;
	position: absolute;
	right: 0;
	
}

.main-hero{
	width: 100%;
	height: 100%;
	position: relative;
	overflow-x: hidden;
}



.front-page {
    /* background-image: url('../../imgs/back4.jpeg'); */
	/* clip-path: polygon(50% 0%, 100% 0, 100% 60%, 50% 100%, 50% 100%, 0% 60%, 0 0); */
	/* background-color: #f2f2f2; */
    height: 100vh;
    width: 100vw;
	

    display: flex;
    
    flex-direction: column;
    overflow-x: hidden;
    
}


.front-top{
	padding: 0 5vw;
    width: 100%;
    height: 100%;
	
    display: flex;
    /* flex-direction: column; */
    align-items: center;
    justify-content: center;
    /* gap: 2rem; */
	text-align: center;
    
}



.home-page-model-left ,.home-page-model-right{
	width: 50%;
	height: 100%;
	position: relative;


	
}

.home-page-model-right{
	
}
.home-page-model-left h1{
	display: none;
	text-shadow: -1px -1px 2px rgba(0,0,0,0.6);
	
	color: #4b4b4b !important;
	font-weight: 500;
}

.home-page-model-left span{
	font-size: 3rem;
	

}


.home-page-model-left{
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 3rem;
	
	justify-content: center;
}





.home-page-heptagon {
    width: 12rem; /* Each heptagon takes up about one-third of the container width */
    height: 12rem; /* Adjust the height to fit three rows, accounting for gap */
    position: absolute; /* Position heptagons absolutely within their parent */
    
    clip-path: polygon(50% 0%, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
	
    box-sizing: border-box; /* Include border in width and height */
}

/* Positioning for each heptagon */
/* First Row */
.home-page-heptagon:nth-child(1) { 
	top: 0; left: 0; 
	background-image: url('../../imgs/homepageimg/1.jpg');
	background-size: cover; /* Ensure the image covers the full area */
	
    background-position: center; /* Center the background image */
}
.home-page-heptagon:nth-child(2) { 
	top: 0; left: 33.33%; 
	background-image: url('../../imgs/homepageimg/2.jpg');
	background-size: cover; /* Ensure the image covers the full area */
    background-position: center; /* Center the background image */
}
.home-page-heptagon:nth-child(3) { 
	top: 0; left: 66.66%; 
	background-image: url('../../imgs/homepageimg/3.jpg');
	background-size: cover; /* Ensure the image covers the full area */
    background-position: center; /* Center the background image */
}
.home-page-heptagon:nth-child(10) { 
	top: 0; left: 99.66%; 
	background-image: url('../../imgs/homepageimg/10.jpg');
	display: none;
	background-size: cover; /* Ensure the image covers the full area */
    background-position: center; /* Center the background image */
}

