.each-project-model::before,
.each-project-model::after {
  position: absolute;
  content: "";
  width: 20%;
  height: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 25px;
  font-weight: bold;
  background-color: #cecdcd;
  transition: all 0.5s;
}

.each-project-model::before {
  top: 0;
  right: 0;
  border-radius: 0 15px 0 100%;
}

.each-project-model::after {
  bottom: 0;
  left: 0;
  border-radius: 0 100%  0 15px;
}

.each-project-model:hover::before,
.each-project-model:hover:after {
  width: 100%;
  height: 100%;
  /* border-radius: 15px; */
  transition: all 0.5s;
}

.each-project-model:hover:after {
  content: "View More";
}

