	display: flex;
	flex-direction: column;
	height: 100%;
	position: absolute;
	transition: 0.2s ease-in-out;
	z-index: 1;
  }

  .img img{
	width: 100%;
	height: 100%;
}
  
  .textBox {
	opacity: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 15px;
	transition: 0.2s ease-in-out;
	z-index: 2;
  }
  
  .textBox > .text {
	font-weight: bold;
  }
  
  .textBox > .head {
	font-size: 4.5vh;
	letter-spacing: 2px;
	text-decoration: underline;
  }
  

  
  .textBox > span {
	font-size: 3vh;
	letter-spacing: 2px;
	color: rgb(0, 0, 0);
  }
  
  .card:hover > .textBox {
	opacity: 1;
  }
  
  .card:hover > .img {
	height: 100%;
	filter: blur(7px);
	/* animation: anim 3s infinite; */
  }
  
  @keyframes anim {
	0% {
	  transform: translateY(0);
	}
  
	50% {
	  transform: translateY(-20px);
	}
  
	100% {
	  transform: translateY(0);
	}
  }
  
